.header__nav {
  display: flex;
  font-family: "Conv_HelveticaNeueCyr-Light", sans-serif; }

.header__nav_item {
  margin: 8px 15px;
  position: relative; }
  .header__nav_item:after {
    content: "";
    position: absolute;
    width: 0;
    border-bottom: 2px solid #8dbf5c;
    bottom: -6px;
    left: 0;
    -webkit-transition: 0.4s;
    -moz-transition: 0.4s;
    -ms-transition: 0.4s;
    -o-transition: 0.4s;
    transition: 0.4s; }
  .header__nav_item:hover:after {
    width: 100%;
    -webkit-transition: 0.4s;
    -moz-transition: 0.4s;
    -ms-transition: 0.4s;
    -o-transition: 0.4s;
    transition: 0.4s; }
  .header__nav_item.has_sub-menu::before {
    content: "";
    position: absolute;
    right: -16px;
    top: 2px;
    border: 5px solid transparent;
    border-top: 5px solid #8dbf5c;
    transform: translateY(50%);
    -webkit-transition: 0.4s;
    -moz-transition: 0.4s;
    -ms-transition: 0.4s;
    -o-transition: 0.4s;
    transition: 0.4s; }
  .header__nav_item.active::before {
    top: 0;
    transform: rotate(180deg);
    -webkit-transition: 0.4s;
    -moz-transition: 0.4s;
    -ms-transition: 0.4s;
    -o-transition: 0.4s;
    transition: 0.4s; }

.current_nav_item:after {
  width: 100%;
  -webkit-transition: 0.4s;
  -moz-transition: 0.4s;
  -ms-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s; }

.sub-menu {
  width: 0;
  overflow: hidden;
  position: absolute;
  top: calc(100% + 20px);
  background: rgba(215, 233, 206, 0.9);
  padding: 10px 0 10px 0;
  left: -20px;
  z-index: 5;
  -webkit-transition: 0.4s;
  -moz-transition: 0.4s;
  -ms-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s; }
  .sub-menu::before {
    content: "";
    position: absolute;
    top: -31px;
    left: 20px;
    border: 25px solid transparent;
    border-bottom: 6px solid rgba(215, 233, 206, 0.9); }

.sub-menu_item {
  padding: 10px 100px 10px 40px;
  width: 100%;
  -webkit-transition: 0.4s;
  -moz-transition: 0.4s;
  -ms-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s; }
  .sub-menu_item:hover {
    background: rgba(242, 242, 242, 0.9);
    -webkit-transition: 0.4s;
    -moz-transition: 0.4s;
    -ms-transition: 0.4s;
    -o-transition: 0.4s;
    transition: 0.4s; }

.sub-menu_item-arrow::before {
  content: "";
  position: absolute;
  right: 15px;
  width: 15px;
  height: 15px;
  background: url("../../img/next.svg");
  background-size: cover;
  opacity: 0.8;
  -webkit-transition: 0.4s;
  -moz-transition: 0.4s;
  -ms-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s; }

.sub-menu_item-arrow.active::before {
  transform: rotate(270deg);
  -webkit-transition: 0.4s;
  -moz-transition: 0.4s;
  -ms-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s; }

.drop-right {
  width: 0;
  overflow: hidden;
  position: absolute;
  padding: 10px 0;
  left: 100%;
  top: 0;
  min-height: 100%;
  background: rgba(242, 242, 242, 0.9);
  -webkit-transition: 0.4s;
  -moz-transition: 0.4s;
  -ms-transition: 0.4s;
  -o-transition: 0.4s;
